import React, { Component } from 'react';
import data from "../resumeData.json";

class About extends Component {
  render() {

      var name = data.main.name;
      var profilepic= data.main.img;
      var bio = data.main.bio;
      var city = data.main.city;
      var state = data.main.state;
      var zip = data.main.zip;
      var phone= data.main.phone;
      var email = data.main.email;  

    return (
      <section id="about">
      <div className="row">
         <div className="three columns">
            <img className="profile-pic"  src={profilepic} alt="Ivan Sanchez Profile" />
         </div>
         <div className="nine columns main-col">
            <h2>Sobre mi:</h2>

            <p>{bio}</p>
            <div className="row">
               <div className="columns contact-details">
                  <h2>Contacto</h2>
                  <p className="address">
						   <span>{name}</span><br />
						   <span>
						         {city} {state}, {zip}
                   </span><br />
						   <span>{phone}</span><br />
                     <span>{email}</span>
					   </p>
               </div>
            </div>
         </div>
      </div>

   </section>
    );
  }
}

export default About;
