import React, { Component } from 'react';

class Footer extends Component {
  render() {

    if(this.props.data){
      var networks= this.props.data.social.map(function(network){
        return <li key={network.name}><a href={network.url}><i className={network.className}></i></a></li>
      })
    }

    return (
      <footer>
     <div className="row">
        <div className="twelve columns">
           <ul className="social-links">
              <li key="linkedin"><a href="https://www.linkedin.com/in/ivan-alberto-sanchez-0a25222b/" target="_blank"><i className="fa fa-linkedin"></i></a></li>
              <li key="instagram"><a href="https://www.instagram.com/ia.sanchez/" target="_blank"><i className="fa fa-instagram"></i></a></li>
              <li key="github"><a href="https://github.com/iasanchez" target="_blank"><i className="fa fa-github"></i></a></li>
           </ul>
           <ul className="copyright">
              <li>&copy; Copyright 2021</li>
           </ul>

        </div>
        <div id="go-top"><a className="smoothscroll" title="Back to Top" href="#home"><i className="icon-up-open"></i></a></div>
     </div>
  </footer>
    );
  }
}

export default Footer;
