import React, { Component } from 'react';
import data from "../resumeData.json";

class Header extends Component {
  render() {
      var name = data.main.name;
      var occupation= data.main.occupation;
      var description= data.main.description;
      var city= data.main.city;

    return (
      <header id="home">
      <nav id="nav-wrap">
         <a className="mobile-btn" href="#nav-wrap" title="Show navigation">Show navigation</a>
	      <a className="mobile-btn" href="#home" title="Hide navigation">Hide navigation</a>
         <ul id="nav" className="nav">
            <li className="current"><a className="smoothscroll" href="#home">Home</a></li>
            <li><a className="smoothscroll" href="#about">Sobre Mi</a></li>
	         <li><a className="smoothscroll" href="#resume">Resumen</a></li>
            <li><a className="smoothscroll" href="#portfolio">Cursos / Intereses</a></li>
            <li><a className="smoothscroll" href="#contact">Contacto</a></li>
         </ul>
      </nav>
      <div className="row banner">
         <div className="banner-text">
            <h1 className="responsive-headline">{name}.</h1>
            <h3><span>{occupation}</span>.<br></br> {description}.</h3>
            <hr />
            <ul className="social">
              <li key="linkedin"><a href="https://www.linkedin.com/in/ivan-alberto-sanchez-0a25222b/" target="_blank"><i className="fa fa-linkedin"></i></a></li>
              <li key="instagram"><a href="https://www.instagram.com/ia.sanchez/" target="_blank"><i className="fa fa-instagram"></i></a></li>
              <li key="github"><a href="https://github.com/iasanchez" target="_blank"><i className="fa fa-github"></i></a></li>
            </ul>
         </div>
      </div>
      <p className="scrolldown">
         <a className="smoothscroll" href="#about"><i className="icon-down-circle"></i></a>
      </p>
   </header>
    );
  }
}
export default Header;
